/* SLICK SLIDER OVERRIDES */
.slick-list {
	margin-left: 25px;
	margin-right: 15px;
}
.slick-prev {
	left: 0;
}
.slick-next {
	right: 25px;
}
.slick-prev:before, .slick-next:before  {
	color: #6D6E70;
}